<template>
    <b-card 
          border-variant="light"
          :header="cardData.name"
          v-if="cardData"
          :img-src="cardData.image_url"
          :img-alt="cardData.name"
          img-top
          tag="article"
          style="max-width: 20rem;"
          class="mb-2">
    <p class="card-text">
      {{cardData.description}}
    </p>
    <button type="button" @click="showMore">Show more</button>
    {{show}}
  </b-card>
</template>

<script>
  export default {
    name: 'card',
    props: {
      cardData: {
        type: Object,
        default: () => {}
      }
    },
    data() {
      return {
        show: false
      }
    },
    methods: {
      showMore($event) {
        console.log($event)
        this.show = !this.show
      }
    }
  }
</script>

<style lang="scss" scoped>
.card {
  margin-top: 25px;
  -webkit-box-shadow: 10px 10px 5px -6px rgba(0,0,0,0.36);
  -moz-box-shadow: 10px 10px 5px -6px rgba(0,0,0,0.36);
  box-shadow: 10px 10px 5px -6px rgba(0,0,0,0.36);
  .card-img-top {
    object-fit: contain;
    max-height: 250px;
    transition: all .2s ease-in-out;
    &:hover {
      transform: scale(1.1); 
    }
  }
}
</style>